import React, { useContext, useState, useCallback } from 'react'

import { FronthubContext } from 'front-hub-context-api'
import { isPromise } from '../../../../core/object'
import { events } from '../../../../core/backbone'

let persistedEventsValue
let globalEvents = events()
const buffer = []
globalEvents.items = globalEvents.items || []

const isInBuffer = value => buffer.find(item => item[0] === value)

export default function useCommunication(component) {
  const { events } = useContext(FronthubContext) // retorna um json do front-hub-config e do manifesto
  const [eventsValue, setEventsValue] = useState(events)
  persistedEventsValue = eventsValue

  if (isPromise(eventsValue)) {
    eventsValue.then(setEventsValue)
  }

  const temp = {
    emit(event, value) {
      console.log('emit called! ', event)

      if (isPromise(eventsValue)) {
        if (isInBuffer('emit')) {
          console.log('is in buffer: emit')
          return null
        }

        buffer.push(['emit', arguments])
        return null
      }
      globalEvents.items.forEach(item => {
        if (item[0] === event) {
          try {
            item[1].call(null, value)
          } catch (e) {
            console.log(e.message)
          }
        }
      })
    },
    listen(event, cb) {
      if (isPromise(eventsValue)) {
        if (isInBuffer('listen')) {
          console.log('is in buffer: listen')
          return null
        }
        buffer.push(['listen', arguments])
        return null
      }

      console.log(`listening event:: ${event}`)

      if (persistedEventsValue && persistedEventsValue.listeners && !persistedEventsValue.listeners.includes(event)) {
        throw new Error('This event was not registered!')
      }

      globalEvents.items.push([event, cb])
    },
  }

  buffer.forEach(item => {
    temp[item[0]].apply(null, item[1])
  })

  return temp
}
